<template>
  <v-btn @click.prevent="logout" dark flat class="text-none">
    <span class="mr-2">Logout</span>
  </v-btn>
</template>

<script>
export default {
  data () {
    return {
    }
  },

  methods: {
    logout () {
      this.$axios.post('/logout').then(response => {
        localStorage.removeItem('access_token')

        this.$router.push('/')
      }).catch(() => {
        localStorage.removeItem('access_token')
        // push notification
      })
    }
  }
}
</script>
